import React, { useState, useEffect } from "react";
import DefaultLayout from "../components/DefaultLayout";
import axios from "axios";
import { Col, Row } from "antd";
import ItemList from "../components/itemList";
import { useDispatch } from "react-redux";

const Homepage = () => {
  const [itemsData, setItemsData] = useState([]);

  const [selectedCategory, setSelectedCategory] = useState("drinks");

  const categories = [
    {
      name: "drinks",
      imageUrl: "https://icons8.com/icon/ClT6dyTsRTDi/champagne",
    },
    {
      name: "rice",
      imageUrl:
        "https://www.licious.in/blog/wp-content/uploads/2020/12/Hyderabadi-chicken-Biryani.jpg",
    },

    {
      name: "noodles",
      imageUrl: "https://icons8.com/icon/13314/noodles",
    },
  ];

  const dispatch = useDispatch();
  useEffect(() => {
    const getAllItemsData = async () => {
      try {
        dispatch({
          type: "SHOW_LOADING",
        });
        const { data } = await axios.get("/api/items/get-item");
        setItemsData(data);
        dispatch({
          type: "HIDE_LOADING",
        });
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };
    getAllItemsData();
  }, [dispatch]);

  return (
    <DefaultLayout>
      <div className="d-flex">
        {categories.map((category) => (
          <div
            key={category.name}
            className={`d-flex category ${
              selectedCategory === category.name && "category-active"
            }`}
            onClick={() => {
              setSelectedCategory(category.name);
            }}
          >
            <h4>{category.name}</h4>
            <img src={category.imageUrl} alt="..." height="60" width="80" />
          </div>
        ))}
      </div>
      <Row>
        {itemsData
          .filter((i) => i.category === selectedCategory)
          .map((items) => (
            <Col xs={24} lg={6} md={12} sm={6}>
              <ItemList key={items.id} item={items} />
            </Col>
          ))}
      </Row>
    </DefaultLayout>
  );
};

export default Homepage;

// can use var and key words..like itemsData
